<template>
  <div id="hobby-interest">
    <h3 class="title-group">爱好兴趣</h3>
    <div class="hobbies">
      <ul class="hobby-title">
        <li>游戏</li>
        <li>小说</li>
        <li>军事</li>
        <li>时尚</li>
      </ul>
      <ul class="hobby-info">
        <li v-for="item in studies">
          <router-link to="hobbyInterest" v-text="item.name"></router-link>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        studies: []
      }
    },
    mounted: function () {
      this.init()
    },
    methods:{
      init: function () {
        var that = this;
        that.$http.get('api/studies').then(function (res) {
          res.json().then(function (result) {
            that.studies = result.studies;
          })
        })
      }
    }
  }
</script>
<style>
  #hobby-interest{
    padding: 5px 20px;
  }
  .hobbies ul{
    float: left;
    margin-left: 20px;
  }
</style>



